@media (min-width: 576px) {
  .container {
    max-width: 540px; } }

@media (min-width: 768px) {
  .container {
    max-width: 720px; } }

@media (min-width: 992px) {
  .container {
    max-width: 960px; }

  .btn.bg-active:hover {
    background-color: lighten($active, 5); }

  .btn.bg-grey:hover {
    background-color: lighten($grey, 5); } }

@media (min-width: 1200px) {
  .container {
    max-width: 1140px; } }


/* ==============================   1366 ================================= */

@media (max-width: 1365.98px) {

  .current-state {

    &-wrap {
      margin-top: 115px; }

    &-block {
      margin-bottom: 37px; } } }


/* ==============================   1200 ================================= */

@media (max-width: 1199.98px) {

  /* Header */

  .header {
    padding-bottom: 106px;

    &__logo {
      margin-bottom: 75px; }

    &__content {
      flex-basis: 60%;
      max-width: 60%; }

    &__img {
      margin-right: 0;
      flex-basis: 40%;
      max-width: 40%; }

    &-content {
      padding-top: 35px; } }


  /* Current-state */

  .current-state {

    &__block {
      flex-basis: 48%;
      max-width: 48%; }

    &-block {

      &__img {
        margin-bottom: 20px;

        img {
          width: 45px; } }

      &__title {
        font-size: 23px; }

      &__text {
        font-size: 17px; } } } }



/* ==============================   992 ================================= */

@media (max-width: 991.98px) {

  /* Section - title */

  .section-title {
    font-size: 36px;

    &:after {
      bottom: -28px; } }

  /* Header */

  .header {
    padding-bottom: 86px;

    &__logo {
      margin-bottom: 45px; }

    &__content, &__img {
      margin-left: auto;
      margin-right: auto; }

    &__content {
      flex-basis: 77%;
      max-width: 77%; }

    &__img {
      order: -1;
      flex-basis: 55%;
      max-width: 55%; }

    &__img, &-content {
      text-align: center; }

    &-content {
      padding-top: 15px;

      &__title {
        margin-bottom: 30px;
        font-size: 30px; }

      &__text {
        margin-bottom: 35px; } } }

  /* Current-state */

  .current-state {

    &-wrap {
      margin-top: 65px; }

    &__block {
      flex-basis: 85%;
      max-width: 85%;
      margin-right: auto;
      margin-left: auto; }

    &-block {
      min-height: auto;
      margin-bottom: 40px;

      &__img {
        margin-bottom: 20px;

        img {
          width: 50px; } }

      &__title {
        font-size: 24px; }

      &__text {
        font-size: 18px; } } } }

/* ==============================   767 ================================= */

@media (max-width: 767.98px) {

  /* Section - title */

  .section-title {
    font-size: 30px;

    &:after {
      bottom: -18px; } }

  /* Header */

  .header {
    padding-bottom: 70px;

    &__logo {
      margin-bottom: 30px; }

    &__content {
      flex-basis: 100%;
      max-width: 100%; }

    &__img {
      flex-basis: 70%;
      max-width: 70%; }

    &-content {

      &__title {
        margin-bottom: 20px;
        font-size: 25px; }

      &__text {
        margin-bottom: 20px;
        font-size: 18px; }

      &__btn:first-child {
        display: block;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 15px; } } }


  /* Current-state */

  .current-state {

    &-wrap {
      margin-top: 50px; }

    &__block {
      flex-basis: 100%;
      max-width: 100%; }

    &-block {
      margin-bottom: 30px;

      &__img {
        margin-bottom: 10px;

        img {
          width: 45px; } }

      &__title {
        font-size: 22px; }

      &__text {
        font-size: 16px; } } } }



/* ==============================   576 ================================= */

@media (max-width: 575.98px) {

  /* Section - title */

  .section-title {
    font-size: 24px;

    &:after {
      bottom: -10px; } }

  /* Header */

  .header {
    padding-bottom: 50px;

    &__img {
      flex-basis: 95%;
      max-width: 350px; }

    &-content {

      &__title, &__text {
        margin-left: auto;
        margin-right: auto; }

      &__title {
        max-width: 460px;
        margin-bottom: 15px;
        font-size: 20px;
        line-height: 28px; }

      &__text {
        max-width: 420px;
        margin-bottom: 20px;
        font-size: 18px;
        line-height: 25px; }

      &__btn:first-child {
        margin-bottom: 10px; } } }

  /* Current-state */

  .current-state {
    padding-top: 10px;
    padding-bottom: 30px;

    &-wrap {
      margin-top: 35px;
      margin-left: 0;
      margin-right: 0; }

    &__block {
      flex-basis: 100%;
      max-width: 100%; }

    &-block {

      &__img {

        img {
          width: 35px; } }

      &__title {
        margin-bottom: 5px;
        font-size: 16.5px;
        font-weight: 500; }

      &__text {
        font-size: 14px;
        text-align: justify; } } } }


/* ==============================   360 ================================= */

@media (max-width: 359.98px) {

  /* Header */

  .header {

    &-content {

      &__title {
        max-width: 300px; } } } }
